st props = defineProps<IEditItemInline>();\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst emit = defineEmits([\"close\"]);\nconst isNameValid = ref<boolean>(false);\nconst isDescriptionValid = ref<boolean>(true);\nconst validations = ref<Record<\"name\" | \"description\", IIDEInputDefinitions[\"validations\"]>>({\n    name: { minAndMaxLength: { min: 3, max: 40 }, availableCharacters: true, nonSpaceCharacter: true },\n    description: { minAndMaxLength: { min: 3, max: 100 }, availableCharacters: true, nonSpaceCharacter: false },\n});\n\nconst name = ref(props.name);\nconst description = ref(props.description);\nfunction edit() {\n    executor.execute(async () => {\n        const objectUseCase = container.resolve<IObjectUseCase>(IObjectUseCase);\n        await objectUseCase.updateObject(props.id, {name: name.value, description: description.value});\n        emit(\"close\");\n    }, { loading: true });\n}\n\nfunction setNameValue(value: string) {\n    name.value = value\n}\nfunction setNameValidationStatus(isValid: boolean) {\n    isNameValid.value = isValid\n}\n\nfunction setDescriptionValue(value: string) {\n    description.value = value\n}\nfunction setDescriptionValidationStatus(isValid: boolean) {\n    isDescriptionValid.value = isValid\n}\n\nconst buttonDisabled = computed(() => !isNameValid.value || !isDescriptionValid.value);\n\n</script> \n\n<template>\n    <div class='editDiv'>\n        <IDEInput :isRequired=\"true\" @getValue=\"setNameValue\" @validateStatus=\"setNameValidationStatus\"\n            :placeholder=\"'Name'\" :type=\"'text'\" info-text=\"Name\" :validations=\"validations.name\" />\n        <div v-if=\"props.type == 'module'\">\n            <IDEInput :isRequired=\"false\" @getValue=\"setDescriptionValue\" @validateStatus=\"setDescriptionValidationStatus\"\n                :placeholder=\"'Description'\" :type=\"'textarea'\" info-text=\"Description\" :validations=\"validations.description\" />\n        </div>\n        <!-- <label> \n            Module Name\n            <input v-model=\"name\" placeholder=\"Name\" />  \n        </label>  -->\n    </div>\n    <div class=\"editButtonDiv\">\n        <button :style=\"{ opacity: buttonDisabled ? '0.5' : '' }\" class='updateBtn'\n                @click=\"edit()\" :disabled=\"buttonDisabled\">Update</button>\n    </div>\n</template> \n\n<style scoped>\n.editDiv {\n    margin-top: 20px;\n    margin-left: 20px;\n    margin-right: 20px;\n}\n\n.editButtonDiv {\n    border-top: 1px solid #E0E0E0;\n    margin-top: 30px;\n    margin-left: 20px;\n    margin-right: 20px;\n    text-align: end;\n}\n\n.updateBtn {\n    margin-top: 20px;\n    margin-bottom: 20px;\n    width: 79px;\n    height: 32px;\n    border-radius: 4px;\n    opacity: 1;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 14px;\n    color: #FFFFFF;\n    letter-spacing: 0.02px;\n    opacity: 1;\n    background-color: #4F537B;\n    border: unset;\n}\n</style>"],"sourceRoot":""}]);
